import nlp from "compromise";

// Tokenización y vocabulario
const vocabulario = new Map();
let indice = 1;

function tokenizarTexto(texto) {
    const doc = nlp(texto);
    return doc.terms().out("array");
}

function agregarAlVocabulario(palabras) {
    palabras.forEach(palabra => {
        if (!vocabulario.has(palabra)) {
            vocabulario.set(palabra, indice);
            indice++;
        }
    });
}

function textoANumeros(texto) {
    const palabras = tokenizarTexto(texto);
    return palabras.map(palabra => vocabulario.get(palabra) || 0);
}

// Cargar el modelo de TensorFlow.js
let model;
async function cargarModelo() {
    model = await tf.loadLayersModel("ruta/al/modelo.json");
}

// Predecir intención
async function predecirIntencion(texto) {
    const numeros = textoANumeros(texto);
    const tensor = tf.tensor2d([numeros]);
    const prediccion = model.predict(tensor);
    const intencion = tf.argMax(prediccion).dataSync()[0];
    return intencion;
}

// Función principal del chatbot
async function simularRespuestaBot(opcion) {
    const loadingDiv = document.createElement("div");
    loadingDiv.classList.add("msg-chat", "bot-typing");
    loadingDiv.innerHTML = "<p>Escribiendo...</p>";
    panelInbox.appendChild(loadingDiv);

    // Predecir la intención
    const intencion = await predecirIntencion(opcion);

    // Enviar la intención al backend
    const response = await fetch("procesarIntencion.php", {
        method: "POST",
        headers: {
            "Content-Type": "application/x-www-form-urlencoded",
        },
        body: `intencion=${intencion}&userInput=${encodeURIComponent(opcion)}`,
    }).then(res => res.json());

    // Mostrar la respuesta
    loadingDiv.remove();
    mostrarRespuestaBot(response.respuesta);

    scrollToBottom();
}

// Cargar el modelo al iniciar
cargarModelo();